import styled from 'styled-components'

const Title = styled.h1`
  color: hsla(148, 31%, 10%, 1);
  text-shadow: -5px -5px 2px hsla(337, 33%, 68%, 0.50),
               -3px -3px 2px hsla(337, 33%, 68%, 0.50),
               -1px -1px 2px hsla(337, 33%, 68%, 0.50);
 `;
 export default Title;



