import styled from 'styled-components'

const Grid = styled.div`
  width: 80em;
  display: flex;
  flex-wrap: wrap;
  padding :1em;
  justify-content: space-around;
  border:1px solid #41ADDD;
  box-shadow: inset 1px 1px 4px #000000;

`;

export default Grid;



