import styled from 'styled-components'

const Container = styled.div`
  display:flex;
  flex-direction: column;
  justify-content: space-around;
  box-shadow: inset 1px 1px 4px #000000;
  margin: 0;
  padding: 1em;
  border: 1px solid #41ADDD;
`;

export default Container;



